import React from 'react';
import './style.css';
import Counter from '@/Components/Counter';

const IndustryInfo = () => {
  return (



    <section className="section industry-info w-full">
      <div className="w-layout-blockcontainer container w-container">
        <div className="review-top-wrapper">
          <div
            className="review-left-col-wrapper"
            style={{
              opacity: 1,
              transform:
                "translate3d(0px, 0px, 0px) scale3d(1, 1, 1) rotateX(0deg) rotateY(0deg) rotateZ(0deg) skew(0deg, 0deg)",
              transformStyle: "preserve-3d",
            }}
          >
            <h2>
              BUNDLED WITH
              <br />
              <span className="short-design-title _02">industry experience</span>
            </h2>
          </div>

          <div className="review-right-col-wrapper">
            <p
              className="review-details"
              style={{
                opacity: 1,
                transform:
                  "translate3d(0px, 0px, 0px) scale3d(1, 1, 1) rotateX(0deg) rotateY(0deg) rotateZ(0deg) skew(0deg, 0deg)",
                transformStyle: "preserve-3d",
              }}
            >
              OUR EXCELLENT TEAM AND PROFESSIONAL TO TAKE CARE OF YOUR BRAND STYLE
            </p>
          </div>
        </div>

        <div className="review-content-wrapper">
          <div className="review-content-wrap">
            <div className="review-details-wrapper">
              <div className="review-details-wrap">
                <div
                  className="review-contents-wrapper"
                  style={{
                    opacity: 1,
                    transform:
                      "translate3d(0px, 0px, 0px) scale3d(1, 1, 1) rotateX(0deg) rotateY(0deg) rotateZ(0deg) skew(0deg, 0deg)",
                    transformStyle: "preserve-3d",
                  }}
                >
                  <div className="review-title">
                    Clients
                    <br />
                    Satisfaction
                  </div>

                  <div className="review-counter-wrapper">
                    <div
                      className="review-counter-number-wrap _01"
                      style={{
                        transform:
                          "translate3d(0px, -89%, 0px) scale3d(1, 1, 1) rotateX(0deg) rotateY(0deg) rotateZ(0deg) skew(0deg, 0deg)",
                        transformStyle: "preserve-3d",
                      }}
                    >
                      {[0, 9, 8, 7, 6, 5, 4, 3, 2].map((num, index) => (
                        <h2 key={index} className="review-counter-number">
                          {num}
                        </h2>
                      ))}
                    </div>
                    <h2 className="icon">+</h2>
                  </div>
                </div>

                <div
                  className="review-contents-wrapper"
                  style={{
                    opacity: 1,
                    transform:
                      "translate3d(0px, 0px, 0px) scale3d(1, 1, 1) rotateX(0deg) rotateY(0deg) rotateZ(0deg) skew(0deg, 0deg)",
                    transformStyle: "preserve-3d",
                  }}
                >
                  <div className="review-title">
                    Years of the
                    <br />
                    experiences
                  </div>

                  <div className="review-counter-wrapper">
                    <div
                      className="review-counter-number-wrap _01"
                      style={{
                        transform:
                          "translate3d(0px, -89%, 0px) scale3d(1, 1, 1) rotateX(0deg) rotateY(0deg) rotateZ(0deg) skew(0deg, 0deg)",
                        transformStyle: "preserve-3d",
                      }}
                    >
                      {[0, 9, 8, 7, 6, 5, 4, 3, 8].map((num, index) => (
                        <h2 key={index} className="review-counter-number">
                          {num}
                        </h2>
                      ))}
                    </div>
                    <h2 className="icon">+</h2>
                  </div>
                </div>
              </div>
              <div className="review-details-wrap">
                <div className="review-contents-wrapper">
                  <h2 className="review-title">Clients Satisfaction</h2>
                  <div className="review-counter-wrapper">
                    <div className="review-counter-number-wrap">
                      <h2 className="review-counter-number">0</h2>
                      <h2 className="review-counter-number">9</h2>
                      <h2 className="review-counter-number">8</h2>
                      <h2 className="review-counter-number">7</h2>
                      <h2 className="review-counter-number">6</h2>
                      <h2 className="review-counter-number">5</h2>
                      <h2 className="review-counter-number">4</h2>
                      <h2 className="review-counter-number">3</h2>
                      <h2 className="review-counter-number">2</h2>
                    </div>
                    <div className="review-counter-number-wrap">
                      <h2 className="review-counter-number">0</h2>
                      <h2 className="review-counter-number">4</h2>
                      <h2 className="review-counter-number">5</h2>
                      <h2 className="review-counter-number">6</h2>
                      <h2 className="review-counter-number">7</h2>
                      <h2 className="review-counter-number">8</h2>
                      <h2 className="review-counter-number">9</h2>
                      <h2 className="review-counter-number">0</h2>
                      <h2 className="review-counter-number">2</h2>
                    </div>
                    <h2 className="icon">+</h2>
                  </div>
                </div>

                <Counter />
              </div>

            </div>
          </div>
        </div>
      </div>
    </section>


  );
};

export default IndustryInfo;
