import React, { useEffect, useState } from 'react';
import { motion } from 'framer-motion';

const Counter = () => {
    const targetNumber = 987; // Target number for the counter
    const [digits, setDigits] = useState([0, 0, 0]);

    useEffect(() => {
        const digitsArray = String(targetNumber).padStart(3, '0').split('').map(Number);
        setDigits(digitsArray);
    }, [targetNumber]);

    const renderCounter = (digit, index) => (
        <div key={index} className='overflow-hidden h-10 w-8 flex flex-col items-center'>
            <motion.div
                initial={{ y: 0 }}
                animate={{ y: -digit * 40 }}
                transition={{ duration: 1, type: 'spring' }}
                className='flex flex-col'>
                {[...Array(10).keys()].map((num) => (
                    <h2 key={num} className='text-2xl font-bold'>
                        {num}
                    </h2>
                ))}
            </motion.div>
        </div>
    );

    return (
        <div className='flex items-center space-x-2'>
            <h2 className='text-3xl font-semibold'>Clients Satisfaction</h2>
            <div className='flex items-center space-x-1'>
                {digits.map((digit, index) => renderCounter(digit, index))}
                <h2 className='text-3xl font-bold'>+</h2>
            </div>
        </div>
    );
};

export default Counter;
